<template>
    <div class="tabs">
      <div class="tab-buttons">
        <button
          v-for="(tab, index) in tabs"
          :key="index"
          @click="selectedTab = index"
          :class="{ active: selectedTab === index }"
        >
          {{ tab.title }}
        </button>
      </div>
      <div class="tab-content">
        <img :src="tabs[selectedTab].image" :alt="tabs[selectedTab].title" />
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'TabImages',
    props: {
      tabs: {
        type: Array,
        required: true,
        default: () => []
      }
    },
    data() {
      return {
        selectedTab: 0
      };
    }
  };
  </script>
  
  <style scoped>
  .tabs {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .tab-buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .tab-buttons button {
    padding: 10px 20px;
    margin: 0 5px;
    background-color: #f0f0f0;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .tab-buttons button.active {
    background-color: #49c1a4;
    color: #fff;
  }
  
  .tab-content {
    text-align: center;
  }
  
  .tab-content img {
    max-width: 100%;
    height: auto;
  }
  </style>
  